@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: lightgray;
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{padding:20px;text-align:left}.won-card{background-color:#4da372}.lost-card{background-color:#a34d5a}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name-section .team-name{font-size:3rem;margin-bottom:20px}.TeamPage .win-loss-section{grid-column:span 1;padding:40px;text-align:center;justify-self:center;align-self:center}.TeamPage .win-loss-section .win-loss{margin-bottom:10px}.TeamPage .latest-match-detail-section{grid-column:span 4}.TeamPage .latest-match-detail-section .latest-match-detail{margin-bottom:10px}.TeamPage .more-link{align-self:center;justify-self:center;font-size:1.5rem}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-match-data{text-align:right}.MatchDetailCard .additional-match-data>p{margin-bottom:8px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-bottom:10px;margin-top:10px}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}

.HomePage .app-name{font-size:3rem;margin:30px auto}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.TeamTile{height:160px;text-align:center;align-self:center;border:1px solid white;padding:20px;background-color:#19388a}.TeamTile .team-card{margin:30px 30px;padding:20px;text-align:center}.TeamTile .team-card .team-card-name{justify-self:center;align-self:center;color:#4f91cd}

