.MatchDetailCard{

    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px;
    margin-bottom: 20px;

    

    .match-date, .match-venue, .match-result {
        margin-top: 10px;
    }
    
    .additional-match-data{
        text-align: right;
        & > p {
            margin-bottom: 8px;
        }
    }
}

